.UsersTable {
  transition: opacity 0.3s ease;
  opacity: 0;
  transform: scale(0);
  pointer-events: none;

  &_visible {
    transform: scale(1);
    opacity: 1;
    pointer-events: all;
  }
}

.headers {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  margin-top: 35px;
  margin-bottom: 16px;
  padding-right: 30px;

  h3 {
    font-family: var(--font-family-main);
    font-size: 24px;
  }
}

.rowsList {
  max-height: 685px;
  padding-right: 30px;
  overflow-y: scroll;

  &::-webkit-scrollbar-track {
    border-radius: 4px;
    background: var(--color-main);
    width: 1px;
  }
  &::-webkit-scrollbar {
    width: 1px;
  }
  &::-webkit-scrollbar-thumb {
    background: var(--color-accent);
    opacity: 0.5;
  }
}

.rowsListItem {
  &:last-child {
    border-bottom: 1px solid var(--color-main);
  }

  span {
    font-family: var(--font-family-descr);
    font-size: 14px;
  }

  &_link,
  &_userItem {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(1, auto);
    grid-gap: 10px;
    padding: 16px 0;
    border-top: 1px solid var(--color-main);
    transition: color 0.3s ease;
  }

  &_link:hover {
    color: var(--color-accent);
  }

  &_userItem {
    color: var(--color-accent);
    position: relative;
  }

  &_userItemBtn {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
}
